.card {
    max-width: 700px;
    margin: auto;
    background: white;
    border-radius: 16px;
    padding: 24px;
    box-shadow: 0 8px 24px rgba(0,0,0,0.1);
}

.container {
    max-width: 1200px;
    margin: 30px auto;
    padding: 24px;
    background: #ffffff;
    border-radius: 16px;
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.08);
}

.title {
    font-size: 22px;
    font-weight: 600;
    margin-bottom: 20px;
    border-bottom: 2px solid #f0f0f0;
    padding-bottom: 10px;
    color: red;
}

.pagination {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 16px;
    margin: 30px 0;
}

.pageBtn {
    width: 6em;
    padding: 10px 18px;
    border-radius: 6px;
    border: 1px solid #ddd;
    background-color: #fff;
    color: #333;
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.2s ease;
}

.pageBtn:hover:not(:disabled) {
    background-color: #000;
    color: #fff;
    border-color: #000;
}

.pageBtn:disabled {
    cursor: not-allowed;
    opacity: 0.5;
    background-color: #f5f5f5;
}

.pageInfo {
    font-size: 14px;
    color: #555;
}