.container {
    width: 350px;
    margin: 80px auto;
    padding: 20px;
    border-radius: 10px;
    background: #ffffff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    text-align: center;
}

.title {
    margin-bottom: 20px;
    font-size: 24px;
    font-weight: 600;
}

.form {
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.input {
    padding: 12px;
    font-size: 16px;
    border-radius: 5px;
    border: 1px solid #ccc;
}

.button {
    padding: 12px;
    font-size: 16px;
    background: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.button:hover {
    background: #0056b3;
}

.alignRight {
    display: flex;
    justify-content: flex-end;
}

.redColor {
    color: red;
}

.otpInput {
    margin-top: 20px;
    margin-bottom: 20px;
    padding: 12px;
    width: 100%;
}

.centeredInput{
    display: flex;
    align-items: center;
    justify-content: center;
}

.success {
    color: green;
}